/* global Office, Excel */
// Force rebuild - version 2.0 with PL-EN support

// API key bƒôdzie pobierany z localStorage przez ka≈ºdego u≈ºytkownika osobno
// U≈ºywamy bardziej niezawodnego proxy (allorigins jest szybszy ni≈º corsproxy)
const CORS_PROXY = "https://api.allorigins.win/raw?url=";
const OPENAI_API_URL = "https://api.openai.com/v1/chat/completions";
const API_URL = CORS_PROXY + encodeURIComponent(OPENAI_API_URL);
const MODEL = "gpt-4o-mini";

const HEADER_ROW = 1;
const BATCH_SIZE = 80;

let glossaryCache = null;         // termEN -> { LANG: TR } (dla EN jako ≈∫r√≥d≈Ço)
let plGlossaryCache = null;       // termPL -> { LANG: TR } (dla PL jako ≈∫r√≥d≈Ço)
let availableLangs = [];          // ["DE","CZ","PL"...] (dostƒôpne dla EN)
let plAvailableLangs = [];        // ["EN"...] (dostƒôpne dla PL)
let langToSheetMap = {};          // LANG -> "EN-LANG"
let plLangToSheetMap = {};        // LANG -> "PL-LANG"

// ---------- Custom alert/confirm (Office.js doesn't support window.alert/confirm) ----------
function customAlert(message) {
  return new Promise((resolve) => {
    const modal = document.getElementById("alertModal");
    const messageEl = document.getElementById("alertMessage");
    const okBtn = document.getElementById("alertOk");

    messageEl.textContent = message;
    modal.classList.remove("hidden");

    const cleanup = () => {
      modal.classList.add("hidden");
      okBtn.onclick = null;
      resolve();
    };

    okBtn.onclick = () => cleanup();
  });
}

function customConfirm(message) {
  return new Promise((resolve) => {
    const modal = document.getElementById("confirmModal");
    const messageEl = document.getElementById("confirmMessage");
    const yesBtn = document.getElementById("confirmYes");
    const noBtn = document.getElementById("confirmNo");

    messageEl.textContent = message;
    modal.classList.remove("hidden");

    const cleanup = (result) => {
      modal.classList.add("hidden");
      yesBtn.onclick = null;
      noBtn.onclick = null;
      resolve(result);
    };

    yesBtn.onclick = () => cleanup(true);
    noBtn.onclick = () => cleanup(false);
  });
}

