// ---------- Show glossary sheets ----------
async function showGlossarySheets() {
  log("Otwieranie arkuszy glosariuszy...");
  
  try {
    await Excel.run(async (ctx) => {
      const sheets = ctx.workbook.worksheets;
      sheets.load("items/name");
      await ctx.sync();

      // Find all EN-XX and PL-XX sheets
      const glossarySheets = sheets.items.filter(s => {
        const nameU = s.name.toUpperCase();
        return (nameU.startsWith("EN-") || nameU.startsWith("PL-")) && nameU.length >= 5;
      });

      if (glossarySheets.length === 0) {
        log("‚ö†Ô∏è Nie znaleziono arkuszy glosariuszy (EN-PL, PL-EN, EN-DE, itp.)");
        log("Stw√≥rz je najpierw u≈ºywajƒÖc 'Dodaj/zaktualizuj parƒô' lub 'Za≈Çaduj podstawowy glosariusz'");
        return;
      }

      // Activate first glossary sheet
      glossarySheets[0].activate();
      await ctx.sync();

      // Log all found sheets
      const sheetNames = glossarySheets.map(s => s.name).join(", ");
      log(`‚úÖ Znaleziono arkusze: ${sheetNames}`);
      log(`üìã Aktywowano: ${glossarySheets[0].name}`);
      log("üí° Mo≈ºesz prze≈ÇƒÖczaƒá siƒô miƒôdzy arkuszami u≈ºywajƒÖc zak≈Çadek na dole Excela");
      
    });
  } catch (error) {
    log(`‚ùå B≈ÇƒÖd: ${error.message}`);
    console.error(error);
  }
}

// ---------- Load basic glossary ----------
async function loadBasicGlossary() {
  const confirmed = await customConfirm(
    "Czy za≈Çadowaƒá podstawowy glosariusz?\n\nTo doda terminy techniczne do arkuszy:\n‚Ä¢ EN ‚Üí PL, DE, FR, ES, IT, RO, UA, RU\n‚Ä¢ PL ‚Üí EN (t≈Çumaczenie polsko-angielskie)"
  );
  
  if (!confirmed) {
    return;
  }

  log("≈Åadowanie podstawowego glosariusza...");

  try {
    // Fetch basic glossary JSON (with cache busting)
    const timestamp = new Date().getTime();
    const response = await fetch(`https://gperyk.github.io/translator-addin-host/glossary/basic-glossary.json?t=${timestamp}`);
    
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    
    const data = await response.json();
    
    if (!data || !data.terms) {
      throw new Error("Nieprawid≈Çowa struktura danych glosariusza. Spr√≥buj ponownie za chwilƒô.");
    }

    let totalAdded = 0;

    await Excel.run(async (ctx) => {
      const sheets = ctx.workbook.worksheets;
      sheets.load("items/name");
      await ctx.sync();

      // Get all terms from JSON
      const allTerms = data.terms;

      // ===== EN-XX sheets (EN jako ≈∫r√≥d≈Ço) =====
      const languages = ["pl", "de", "fr", "es", "it", "ro", "ua", "ru"];
      
      for (const lang of languages) {
        const langUpper = lang.toUpperCase();
        const sheetName = `EN-${langUpper}`;
        
        // Find or create sheet
        let sheet = sheets.items.find(s => s.name.toUpperCase() === sheetName);
        let isNewSheet = false;
        if (!sheet) {
          sheet = sheets.add(sheetName);
          sheet.getRange("A1:B1").values = [["EN", langUpper]];
          await ctx.sync();
          isNewSheet = true;
        }

        // Get existing data
        let existingData = [];
        let existingTerms = new Set();
        
        try {
          const used = sheet.getUsedRange();
          if (used) {
            used.load("values,rowCount");
            await ctx.sync();
            existingData = used.values || [];
            
            // If sheet has data (more than just header)
            if (existingData.length > 1) {
              for (let i = 1; i < existingData.length; i++) {
                const en = (existingData[i][0] || "").toString().trim().toLowerCase();
                if (en) existingTerms.add(en);
              }
            }
          }
        } catch (e) {
          // Sheet is empty or new, that's OK
          console.log(`Sheet ${sheetName} is new or empty`);
        }

        // Add new terms
        const termsToAdd = [];
        for (const term of allTerms) {
          // Check if term has translation for this language
          if (term[lang] && term[lang].trim() !== "") {
            const enLower = (term.en || "").toString().trim().toLowerCase();
            if (enLower && !existingTerms.has(enLower)) {
              termsToAdd.push([term.en, term[lang]]);
              totalAdded++;
            }
          }
        }

        if (termsToAdd.length > 0) {
          // Calculate start row: if sheet has only header (1 row), start at row 2
          const startRow = existingData.length > 1 ? existingData.length + 1 : 2;
          const range = sheet.getRangeByIndexes(startRow - 1, 0, termsToAdd.length, 2);
          range.values = termsToAdd;
          await ctx.sync();
          log(`‚úÖ Dodano ${termsToAdd.length} termin√≥w do ${sheetName}`);
        } else {
          // Log what happened
          if (isNewSheet) {
            log(`üìã Utworzono arkusz ${sheetName} - sprawdzanie termin√≥w...`);
            // Count how many terms have this language
            let termsWithLang = 0;
            for (const term of allTerms) {
              if (term[lang] && term[lang].trim() !== "") termsWithLang++;
            }
            if (termsWithLang > 0) {
              log(`‚ö†Ô∏è Znaleziono ${termsWithLang} termin√≥w z t≈Çumaczeniem ${lang.toUpperCase()}, ale wszystkie ju≈º istniejƒÖ w arkuszu.`);
            } else {
              log(`‚ÑπÔ∏è Brak termin√≥w z t≈Çumaczeniem ${lang.toUpperCase()} w glosariuszu.`);
            }
          }
        }
      }

      // ===== PL-EN sheet (PL jako ≈∫r√≥d≈Ço) =====
      if (data.pl_en_terms && data.pl_en_terms.length > 0) {
        const plEnSheetName = "PL-EN";
        
        // Find or create PL-EN sheet
        let plEnSheet = sheets.items.find(s => s.name.toUpperCase() === plEnSheetName);
        let isNewPlEnSheet = false;
        if (!plEnSheet) {
          plEnSheet = sheets.add(plEnSheetName);
          plEnSheet.getRange("A1:B1").values = [["PL", "EN"]];
          await ctx.sync();
          isNewPlEnSheet = true;
        }

        // Get existing data
        let existingPlData = [];
        let existingPlTerms = new Set();
        
        try {
          const usedPl = plEnSheet.getUsedRange();
          if (usedPl) {
            usedPl.load("values,rowCount");
            await ctx.sync();
            existingPlData = usedPl.values || [];
            
            if (existingPlData.length > 1) {
              for (let i = 1; i < existingPlData.length; i++) {
                const pl = (existingPlData[i][0] || "").toString().trim().toLowerCase();
                if (pl) existingPlTerms.add(pl);
              }
            }
          }
        } catch (e) {
          console.log(`Sheet ${plEnSheetName} is new or empty`);
        }

        // Add new PL-EN terms
        const plEnTermsToAdd = [];
        for (const term of data.pl_en_terms) {
          if (term.pl && term.en) {
            const plLower = term.pl.toString().trim().toLowerCase();
            if (plLower && !existingPlTerms.has(plLower)) {
              plEnTermsToAdd.push([term.pl, term.en]);
              totalAdded++;
            }
          }
        }

        if (plEnTermsToAdd.length > 0) {
          const startRow = existingPlData.length > 1 ? existingPlData.length + 1 : 2;
          const range = plEnSheet.getRangeByIndexes(startRow - 1, 0, plEnTermsToAdd.length, 2);
          range.values = plEnTermsToAdd;
          await ctx.sync();
          log(`‚úÖ Dodano ${plEnTermsToAdd.length} termin√≥w do PL-EN`);
        } else if (isNewPlEnSheet) {
          log(`üìã Utworzono arkusz PL-EN`);
        }
      }
    });

    await refreshGlossaryAll();
    log(`‚úÖ Dodano ${totalAdded} nowych termin√≥w do glosariuszy!`);
    
    // Automatically show glossary sheets after loading
    if (totalAdded > 0) {
      log("Otwieranie arkuszy glosariuszy...");
      await showGlossarySheets();
    }
    
  } catch (error) {
    log(`‚ùå B≈ÇƒÖd: ${error.message}`);
    console.error(error);
  }
}

// ---------- glossary from EN-XX and PL-XX sheets ----------
async function loadGlossaryFromAllSheets() {
  await Excel.run(async (ctx) => {
    const sheets = ctx.workbook.worksheets;
    sheets.load("items/name");
    await ctx.sync();

    // EN-XX glossaries (EN jako ≈∫r√≥d≈Ço)
    const enMap = {};              // termEN -> { LANG: translation }
    const enLangsSet = new Set();
    const enL2s = {};

    // PL-XX glossaries (PL jako ≈∫r√≥d≈Ço)
    const plMap = {};              // termPL -> { LANG: translation }
    const plLangsSet = new Set();
    const plL2s = {};

    for (const sh of sheets.items) {
      const nameU = sh.name.toUpperCase();

      // EN-DE / EN-CZ / EN-RO / EN-PL ...
      if (nameU.startsWith("EN-") && nameU.length >= 5) {
        const tgtLangRaw = nameU.slice(3).trim();
        const tgtLang = normalizeHeader(tgtLangRaw);
        if (!tgtLang) continue;

        enLangsSet.add(tgtLang);
        enL2s[tgtLang] = sh.name;

        const used = sh.getUsedRange();
        used.load("values,rowCount,columnCount");
        await ctx.sync();

        const vals = used.values || [];
        if (vals.length < 2) continue;

        const hdr = vals[0].map(h => normalizeHeader(h));
        const enIx = hdr.indexOf("EN");
        const tgtIx = hdr.indexOf(tgtLang);

        if (enIx < 0 || tgtIx < 0) continue;

        for (let r = 1; r < vals.length; r++) {
          const termEN = (vals[r][enIx] || "").toString().trim();
          const tr = (vals[r][tgtIx] || "").toString().trim();
          if (!termEN || !tr) continue;

          if (!enMap[termEN]) enMap[termEN] = {};
          enMap[termEN][tgtLang] = tr;
        }
      }

      // PL-EN / PL-DE / PL-XX ...
      if (nameU.startsWith("PL-") && nameU.length >= 5) {
        const tgtLangRaw = nameU.slice(3).trim();
        const tgtLang = normalizeHeader(tgtLangRaw);
        if (!tgtLang) continue;

        plLangsSet.add(tgtLang);
        plL2s[tgtLang] = sh.name;

        const used = sh.getUsedRange();
        used.load("values,rowCount,columnCount");
        await ctx.sync();

        const vals = used.values || [];
        if (vals.length < 2) continue;

        const hdr = vals[0].map(h => normalizeHeader(h));
        const plIx = hdr.indexOf("PL");
        const tgtIx = hdr.indexOf(tgtLang);

        if (plIx < 0 || tgtIx < 0) continue;

        for (let r = 1; r < vals.length; r++) {
          const termPL = (vals[r][plIx] || "").toString().trim();
          const tr = (vals[r][tgtIx] || "").toString().trim();
          if (!termPL || !tr) continue;

          if (!plMap[termPL]) plMap[termPL] = {};
          plMap[termPL][tgtLang] = tr;
        }
      }
    }

    // Zapisz cache dla EN jako ≈∫r√≥d≈Ço
    glossaryCache = enMap;
    availableLangs = Array.from(enLangsSet).sort();
    langToSheetMap = enL2s;

    // Zapisz cache dla PL jako ≈∫r√≥d≈Ço
    plGlossaryCache = plMap;
    plAvailableLangs = Array.from(plLangsSet).sort();
    plLangToSheetMap = plL2s;
  });

  return glossaryCache;
}

// ---------- add/update glossary pair (and create EN-XX if needed) ----------
async function addOrUpdateGlossaryPair() {
  const selLang = normalizeHeader(document.getElementById("gLangSelect").value);
  const newLangRaw = document.getElementById("gNewLang").value.trim();
  const newLang = normalizeHeader(newLangRaw);

  const sourceLang = document.getElementById("sourceLangSelect").value || "EN";
  const termSource = document.getElementById("gEn").value.trim();
  const tr = document.getElementById("gTr").value.trim();

  const tgtLang = newLang || selLang;

  if (!tgtLang) {
    log("Wybierz jƒôzyk z listy albo wpisz nowy (np. PL lub EN).");
    return;
  }
  if (!termSource || !tr) {
    log(`Uzupe≈Çnij frazƒô ${sourceLang} i t≈Çumaczenie.`);
    return;
  }

  await Excel.run(async (ctx) => {
    const sheets = ctx.workbook.worksheets;
    sheets.load("items/name");
    await ctx.sync();

    const sheetName = `${sourceLang}-${tgtLang}`;
    let glossSheet;

    // find or create XX-YY sheet
    const existing = sheets.items.find(s => s.name.toUpperCase() === sheetName);
    if (existing) {
      glossSheet = existing;
    } else {
      glossSheet = sheets.add(sheetName);
      glossSheet.getRange("A1:B1").values = [[sourceLang, tgtLang]];
      await ctx.sync();
    }

    const used = glossSheet.getUsedRange();
    used.load("values,rowCount");
    await ctx.sync();

    const vals = used.values || [];
    const hdr = vals[0].map(h => normalizeHeader(h));
    const srcIx = hdr.indexOf(sourceLang);
    const tgtIx = hdr.indexOf(tgtLang);

    if (srcIx < 0 || tgtIx < 0) {
      log(`B≈ÅƒÑD: Arkusz ${sheetName} musi mieƒá nag≈Ç√≥wki ${sourceLang} oraz ${tgtLang} w wierszu 1.`);
      return;
    }

    // search row to update
    let foundRow = -1;
    for (let r = 1; r < vals.length; r++) {
      const src0 = (vals[r][srcIx] || "").toString().trim().toLowerCase();
      if (src0 === termSource.toLowerCase()) {
        foundRow = r + 1; // 1-based
        break;
      }
    }

    if (foundRow > 0) {
      // update
      const colLetter = indexToCol(tgtIx + 1);
      glossSheet.getRange(`${colLetter}${foundRow}`).values = [[tr]];
    } else {
      // append
      const nextRow = vals.length + 1;
      const srcColLetter = indexToCol(srcIx + 1);
      const tgtColLetter = indexToCol(tgtIx + 1);
      glossSheet.getRange(`${srcColLetter}${nextRow}`).values = [[termSource]];
      glossSheet.getRange(`${tgtColLetter}${nextRow}`).values = [[tr]];
    }

    await ctx.sync();
  });

  // refresh cache & UI
  await refreshGlossaryAll();

  // clear inputs except lang
  document.getElementById("gEn").value = "";
  document.getElementById("gTr").value = "";
  document.getElementById("gNewLang").value = "";

  log(`Zapisano glosariusz: ${termEN} -> ${tgtLang}:${tr}`);
}

// ---------- glossary list (FILTERED BY DROPDOWN) ----------
function renderGlossaryList() {
  const el = document.getElementById("gList");
  if (!el) return;

  // Wybierz odpowiedni cache w zale≈ºno≈õci od jƒôzyka ≈∫r√≥d≈Çowego
  const sourceLang = document.getElementById("sourceLangSelect").value || "EN";
  const map = sourceLang === "PL" ? (plGlossaryCache || {}) : (glossaryCache || {});
  
  const selectedLang = normalizeHeader(
    document.getElementById("gLangSelect").value
  );

  const lines = [];

  for (const term of Object.keys(map)) {
    const langsObj = map[term] || {};

    if (!selectedLang) {
      // pokaz wszystko
      for (const lang of Object.keys(langsObj)) {
        lines.push(`${term}  |  ${lang}  ‚Üí  ${langsObj[lang]}`);
      }
    } else {
      // filtr po wybranym jƒôzyku
      if (langsObj[selectedLang]) {
        lines.push(`${term}  ‚Üí  ${langsObj[selectedLang]}`);
      }
    }
  }

  el.textContent = lines.length
    ? lines.join("\n")
    : "(brak wpis√≥w dla tego jƒôzyka)";
}

// ---------- glossary tokenization (whole word/phrase only) ----------
function applyGlossaryTokens(lines, tgtLang, glossaryMap) {
  const tokenMap = {}; // token -> translation
  const terms = Object.keys(glossaryMap || {}).sort((a,b)=>b.length-a.length);

  const tokenizedLines = lines.map(line => {
    let out = line;

    for (const term of terms) {
      const tr = glossaryMap[term]?.[tgtLang];
      if (!tr) continue;

      const token = `[[G${Object.keys(tokenMap).length + 1}]]`;

      const safe = escapeRegex(term);
      const re = new RegExp(`\\b${safe}\\b`, "gi"); // only whole word/phrase
      out = out.replace(re, token);

      tokenMap[token] = tr;
    }
    return out;
  });

  return { tokenizedLines, tokenMap };
}

function restoreGlossaryTokens(line, tokenMap) {
  let out = line;
  for (const token of Object.keys(tokenMap)) {
    out = out.split(token).join(tokenMap[token]);
  }
  return out;
}

// ---------- utils ----------
function normalizeHeader(v) {
  return (v ?? "").toString().replace(/\u00A0/g, " ").trim().toUpperCase();
}

function escapeRegex(s) {
  return s.replace(/[.*+?^${}()|[\]\\]/g, "\\$&");
}

// 1-based column index -> Excel letters
function indexToCol(n) {
  let s = "";
  while (n > 0) {
    let m = (n - 1) % 26;
    s = String.fromCharCode(65 + m) + s;
    n = Math.floor((n - 1) / 26);
  }
  return s;
}